const Wrapper = styled.div`
  position: fixed;
  top: 0;
  right: 0;
  width: 300px;
  height: 55px;
  border-radius: 20px;
  background-color: ${Color.light};
  padding: 10px;
  margin: 10px;
  overflow: hidden;
`;

const Nav = styled.nav`
  display: flex;
  width: 100%;
  a {
    font-weight: bold;
    color: ${Color.primary};
  }
`;

const LinkStyle = styled(Link)`
  text-align: center;
  flex: 1;
  text-decoration: none;
  color: ${Color.dark} !important;

  &::after {
    content: "Home";
    display: block;
    margin-top: 10px;
    transition: 0.2s;
  }
  &:hover::after {
    transform: translateY(-20px);
  }
  &:nth-child(2)::after {
    content: "Technologie";
  }
  &:nth-child(3)::after {
    content: "O mnie";
  }
`;

const AnimLink = posed(LinkStyle)({
  hoverable: true,
  pressable: true,
  init: { y: 0 },
  hover: { y: -10 },
  press: { y: -5 },
});

function Navigation() {
  return (
    <Wrapper>
      <Nav>
        <IconContext.Provider
          value={{
            style: {
              color: Color.dark,
              fontSize: 35,
              flex: 1,
              margin: `10px 0`,
            },
          }}
        >
          <AnimLink to="/">
            <TiHome />
          </AnimLink>
          {/*  */}
          <AnimLink to="/skills">
            <FaFire />
          </AnimLink>
          {/*  */}
          <AnimLink to="/omnie">
            <IoMdPerson />
          </AnimLink>
          {/*  */}
        </IconContext.Provider>
      </Nav>
    </Wrapper>
  );
}
export default Navigation;
